import React, { useContext, useState, useEffect } from "react";
import * as ROUTES from "../helpers/ROUTES";
import { Link, Routes, useNavigate } from "react-router-dom";
import styled, { keyframes } from "styled-components";
import { AuthContext } from "../contexts/authContext";
import PostsService from "../services/PostsService";
import Post from "../components/Post";
import { NotificationsContext } from "../contexts/notifyContext";
import { ClipLoader } from "react-spinners";
import { uploadImage } from "../services/firebase";
import ImageIcon from "@mui/icons-material/Image";

const DashboardContainer = styled.div`
    width: 55%;
    @media screen and (max-width: 750px) {
        width: 90%;
    }
`;

const TogglePostButton = styled.button`
    cursor: pointer;
    background-color: ${(props) =>
        props.show ? props.theme.red : props.theme.buttonColour};
    color: white;
    width: 100%;
    padding: 10px;
    border-radius: 5px;
`;
const fadeIn = keyframes`
  0% {
   transform: translateY(0px);
  }
  100% {
   transform: translateY(-100px);
  }
`;
const PostForm = styled.div`
    //border: 1px solid ${({ theme }) => theme.mainFontColour};
    background-color: ${(props) => props.theme.cardBg};
    border-top: none;
    color: ${({ theme }) => theme.mainFontColour};
    width: 100%;
    padding: 10px;
    // box-shadow: ${(props) => props.theme.shadowColour} 0px 2px 2px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transform: ${(props) =>
        props.show ? "translateY(0px)" : "translateY(-150px)"};
    height: ${(props) => (props.show ? "auto" : "150px")};
    align-items: center;
    animation-iteration-count: 1;
`;

const PostTextArea = styled.textarea`
    background-color: transparent;
    width: 100%;
    padding: 10px;
    resize: none;
    color: ${({ theme }) => theme.mainFontColour};
    border-bottom: 1px solid ${({ theme }) => theme.mainFontColour};
`;
const OverflowHidden = styled.div`
    overflow: hidden;
    background-color: transparent;
    margin-bottom: 20px;
    border-radius: 10px;
    box-shadow: ${(props) =>
        props.show ? `${props.theme.shadowColour} 0px 2px 2px` : ""};
`;

const PostSubmit = styled.button`
    cursor: pointer;
    background-color: ${({ theme }) => theme.buttonColour};
    color: white;
    margin-top: 10px;
    width: 30%;
    padding: 10px;
    border-radius: 5px;
    @media screen and (max-width: 750px) {
        width: 90%;
    }
`;
const PostsContainer = styled.div`
    background-color: transparent;
    transform: ${(props) =>
        props.show ? "translateY(0)" : "translateY(-150px)"};
`;

const NoPostForm = styled.div`
    text-align: center;
    font-size: 30px;
`;
const LoaderDiv = styled.div`
    display: flex;

    align-items: center;
    justify-content: center;
`;
const Input = styled.input`
    background-color: ${(props) => props.theme.bodyBg};
    border: 1px solid ${(props) => props.theme.borderColour};
    padding: 6px;
    margin: 5px 0 10px 0;
    color: ${({ theme }) => theme.mainFontColour};
    width: 100%;
`;

const ImgInput = styled(Input)`
    display: none;
`;
const Imglabel = styled.label`
    background-color: ${({ theme }) => theme.bodyBg};
    border: 1px solid ${({ theme }) => theme.borderColour};
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    padding: 6px;
    margin: 5px 0 10px 0;
    color: ${({ theme }) => theme.mainFontColour};
    cursor: pointer;
    width: 100%;
`;

const Dashboard = () => {
    const [showPostForm, setShowPostForm] = useState(false);
    const [PostValue, setPostValue] = useState("");
    const [posts, setPosts] = useState([]);
    const { Open } = useContext(NotificationsContext);
    const [loading, setLoading] = useState(false);
    const [imageFile, setImageFile] = useState(null);

    function togglePostForm() {
        return setShowPostForm(!showPostForm);
    }

    useEffect(() => {
        async function getPosts() {
            setLoading(true);
            let response = await PostsService.getPosts();
            setPosts(response.data);
            setLoading(false);
        }
        getPosts();
        // console.log(posts);
        return () => {};
    }, []);

    function handleDeletePost(id) {
        setPosts(posts.filter((post) => post._id !== id));
        async function del() {
            await PostsService.deletePost(id);
        }
        Open("Post deleted");
        del();
    }

    // useEffect(() => {
    //     // console.log(posts);
    //     return () => {};
    // }, [posts]);

    return (
        <DashboardContainer>
            <TogglePostButton
                show={showPostForm}
                onClick={() => {
                    togglePostForm();
                }}
            >
                {showPostForm ? "Close" : "Post something"}
            </TogglePostButton>

            <OverflowHidden show={showPostForm}>
                <PostForm show={showPostForm}>
                    <Imglabel htmlFor="profileImg">
                        {imageFile?.name || (
                            <>
                                <ImageIcon /> Add an image
                            </>
                        )}
                    </Imglabel>
                    <ImgInput
                        onChange={(e) => setImageFile(e.target.files[0])}
                        accept="image/png, image/jpeg"
                        type="file"
                        id="profileImg"
                    />
                    <PostTextArea
                        onChange={(e) => {
                            setPostValue(e.target.value);
                        }}
                        value={PostValue}
                        rows={3}
                        placeholder="What's on your mind?"
                    />
                    <PostSubmit
                        disabled={!PostValue || loading}
                        onClick={async () => {
                            setLoading(true);
                            let imgSrc;
                            if (imageFile) {
                                let res = await uploadImage(imageFile);
                                imgSrc = res.imageSrc;
                            }
                            let res = await PostsService.makePost(
                                PostValue,
                                imgSrc,
                                imageFile?.name
                            );
                            //console.log(res.data);
                            // let data = await res.json();
                            // console.log(data);
                            Open("Post succesufully added");
                            setPostValue("");
                            let arr = [
                                { ...res.data, user: { _id: res.data.user } },
                                ...posts,
                            ];
                            //console.log(arr);
                            setPosts(arr);
                            setLoading(false);
                            setShowPostForm(false);
                        }}
                    >
                        {loading ? "Please wait..." : "Post"}
                    </PostSubmit>
                </PostForm>
            </OverflowHidden>
            <PostsContainer show={showPostForm}>
                {posts.length != 0 ? (
                    posts.map((post) => {
                        return (
                            <Post
                                key={post._id}
                                handleDeletePost={handleDeletePost}
                                post={post}
                            />
                        );
                    })
                ) : loading ? (
                    <LoaderDiv>
                        <ClipLoader
                            color="lightblue"
                            loading={true}
                            size={150}
                        />
                    </LoaderDiv>
                ) : (
                    <NoPostForm>No posts</NoPostForm>
                )}
            </PostsContainer>
        </DashboardContainer>
    );
};

export default Dashboard;
